* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

body {
  background: #eee;
}

@media (min-width: 500px) {
  .globalHeader {
    margin: 100px 0;
  }

}

.searchForm {
  display: flex;
  justify-content: space-between;
}

@media (min-width: 500px) {
  .searchForm {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
}

.searchForm>input {
  width: 100%;
  height: 40px;
  margin-right: 20px;
  padding: 0 10px;
  border: none;
  border-radius: 4px;
}

.searchForm>button {
  white-space: nowrap;
  padding: 0 27px;
  border: 4px;
  border: none;
  border-radius: 4px;
  background: rgba(2, 130, 185, 1);
  color: white;
}

.globalMain {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

.siteList {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media(min-width: 500px) {
  .siteList {
    margin-left: 0;
    margin-right: -25px;
    justify-content: flex-start;
  }
}

.siteList>li {
}

@media(min-width: 500px){
  .siteList>li {
    margin-right: 25px;
  }
}

.siteList .site {
  width: 160px;
  height: 146px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: white;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px 0;
  position: relative;
  cursor: pointer;
}

.siteList .site>.logo {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
  text-transform: uppercase;
}

.siteList .site>.link {
  font-size: 14px;
  margin-top: 4px;
}

.siteList .site>.close {
  position: absolute;
  right: 10px;
  top: 10px;
  display: none;
  cursor: default;
}

.siteList .site:hover>.close {
  display: block;
}

.siteList .addButton {
  width: 160px;
}

.siteList .addButton {
  width: 160px;
  height: 146px;
  background: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 0;
}

.siteList .addButton .icon {
  width: 64px;
  height: 64px;
}

.siteList .addButton .iconWrapper {
  width: 64px;
  height: 64px;
}

.siteList .addButton .text {
  font-size: 14px;
  margin-top: 4px;
}